body {
	overscroll-behavior-x: none;
	overscroll-behavior-y: none;
}
.flags {
	font-family: 'BabelStone Flags';
	font-size: 14px;
	text-shadow: 0 0 0 black;
}
.warehouse-title {
	position: relative;
	margin-top: 11px;
	&:before {
		position: absolute;
		content: '';
		bottom: -2px;
		width: 141px;
		height: 4px;
		background: rgba(81, 81, 81, 0.7);
		border-radius: 2px;
	}
}
.warehouse-nav {
	margin-top: 53px;
	font-weight: 400;
	ul {
		list-style: none;
		padding: 0;
		margin: 0;
		font-size: 14px;
		li {
			height: 20px;
			line-height: 20px;
			padding-left: 12px;
			margin-left: 2px;
			width: 127px;
			cursor: pointer;
		}
	}
	.select-link {
		background: rgba(81, 81, 81, 0.7);
		color: rgba(255, 255, 255, 0.9);
		position: relative;
		&:before {
			content: '';
			position: absolute;
			width: 8px;
			height: 20px;
			left: -2px;
			background: #515151;
			border-radius: 2px 0 0 2px;
		}
	}
}


.warehouse-table {
	// transform: translateZ(0px);
	// transform: scale(1);
	// -webkit-backface-visibility: hidden;
	// -webkit-perspective: 1000;
	transform: translate3d(0, 0, 0);
	will-change: transform, scroll-position;
	transform-origin: 0 0;
}
