.warehouse-products {
	margin-left: 30px;
	// width: 970px;
	// width: 1150px;
	width: 100%;
	width: calc(100vw - 350px);
	// width: 1035px;
	position: relative;
	.warehouse-input {
		// max-width: 60px;
		position: relative;
		height: 20px;
		input {
			width: 100%;
			border: none;
			padding: 0;
			outline: none;
			font-weight: 300;
			text-overflow: ellipsis;
			height: 20px;
		}
		.underline {
			transition: 0.2s;
			width: 100%;
			background-color: #9c9b9e;
			height: 1px;
			bottom: 2px;
			left: 0;
			z-index: 4;
			position: absolute;
		}
	}



	.warehouse-dropmenu {
		width: 100%;
		height: 100%;
		position: relative;
		z-index: 9999;
		display: flex;

		cursor: pointer;
		input:not(.min,.max, .inputDataMin, .inputDataMax) {
			outline: none;
			border: none;
			font-weight: 300;
			padding: 0;
			text-overflow: ellipsis;
			height: 20px;
			// width: calc(100% - 10px);
			width: 100%;
			box-sizing: border-box;
			padding-right: 18px;
		}
		&:before {
			background-image: url('../../img/arrow-down.svg');
			content: '';
			background-repeat: no-repeat;
			background-size: contain;
			width: 9px;
			height: 4px;
			position: absolute;
			right: 0;
			top: 6px;
			// opacity: 0.8;
		}
		.countBlock {
			position: relative;
			right:16px;
			color: rgba(0, 0, 0, 0.5);
			span:last-child {
				font-weight: 500;
			}	
		}
		.sortBtn {
			position: absolute;
			right:3px;
			display: none;
			transition: 0.2s;
			transform: rotate(180deg);
			svg {
				position: relative;
				top:1px;
				fill-opacity: 0.5;
				transition: 0.2s;
			}
			&:hover svg {
				opacity: 0.5;
			}
		}
		.sortBtn.on {
			display: block !important;
		}
		.sortBtn.active {
			display: block !important;
		}
		.sortBtn.arrowTransform {
			svg {
				transform : scaleY(-1) !important;
			}
		}
		.text-result,
		.status-result {
			text-align: center;
			height: 20px;
			line-height: 18px;
			overflow: hidden;
			text-overflow: ellipsis;
			width: calc(100% - 10px);
			label {
				float: left;
				top: 5px;
				left:3px;
			}
		}
		.text-result {
			text-align: center;
			height: 20px;
			line-height: 20px;
			overflow: hidden;
			text-overflow: ellipsis;
			width: calc(100% - 10px);
		}
		.underline {
			transition: 0.2s;
			width: 100%;
			background-color: #9c9b9e;
			height: 1px;
			bottom: 2px;
			left: 0;
			z-index: 4;
			position: absolute;
		}
		.dropmenu {
			background: white;
			z-index: 3;
			color: black;
			visibility: hidden;
			opacity: 0;
			position: absolute;
			text-align: left;
			height: 90px;
			// height: 83px;
			box-shadow: 4px 4px 9px rgb(0 0 0 / 15%);
			width: 100%;
			top: 0;
			transform: translateY(0px);
			overflow-x: hidden;
			.scrollOff {
				&::-webkit-scrollbar {
					width: 0;
				}
				&::-webkit-scrollbar-track {
					background: transparent;
					box-shadow: none;
				}
				&::-webkit-scrollbar-thumb {
					background-color: transparent;
					box-shadow: none;
				}
			}

			.simplebar-track.simplebar-vertical {
				background-color: rgba(0, 0, 0, 0.1);
				width: 3px;
				margin-top: 5px;
				margin-bottom: 5px;
				margin-right: 5px;
				// bottom: 0;
				border-radius: 5px;
				div {
					transition: transform 0s !important;
				}
			}
			.simplebar-track.simplebar-vertical .simplebar-scrollbar::before {
				background: rgba(0, 0, 0, 0.3);
				width: 3px;
				border-radius: 5px;
				left: 0;
				right: 0;
				bottom: 0;
				top: 0;
			}
			.simplebar-content {
				display: block;
			}
			.simplebar-scrollbar.simplebar-visible:before {
				opacity: 1;
			}
			.simplebar-content-wrapper {
				padding: 0;
			}

			.list {
				// list-style: none;
				padding-left: 15px;
				padding-right: 10px;
				height: 18px;
				line-height: 18px;
				// display: flex;
				// align-items: center;
				white-space: nowrap;
				position: relative;
				overflow: hidden;
				// display: block;
				text-overflow: ellipsis;
				.status {
					margin: 0 2px;
				}
			}
			.infinity-list {
				padding-left: 15px;
				padding-right: 10px;
				height: 18px;
				line-height: 18px;
				// display: flex;
				// align-items: center;
				white-space: nowrap;
				position: relative;
				overflow: hidden;
				// display: block;
				text-overflow: ellipsis;
				width: calc(100% - 25px) !important;
			}
			.findUnderline {
				position: relative;
				&:before {
					content: '';
					height: 2px;
					border-radius: 2px;
					width: 100%;
					position: absolute;
					bottom: 0px;
					background-color: grey;
				}
			}
			// li:first-child {
			// 	overflow: visible;
			// }
			.flags {
				color: rgba(0, 0, 0, 1) !important;
			}
			.list:hover,
			.infinity-list:hover {
				color: rgba(0, 0, 0, 0.5);
			}
			.list:hover .status,
			.infinity-list:hover .status {
				opacity: 0.5;
			}
			.list:hover .flags,
			.infinity-list:hover .flags {
				opacity: 0.5;
			}
			.select-btn:before {
				content: '';
				width: 4px;
				height: 4px;
				top: 7px;
				left: 6px;
				border-radius: 100%;
				position: absolute;
				background-color: #787885;
			}
			// li:first-child {
			// 	margin-top: 5px;
			// }
		}
		.toggle {
			// top: 20px;
			transform: translateY(20px);
			visibility: visible;
			opacity: 1;
			transition: transform 0.3s, opacity 0.3s;
		}
		.toggleAdaptive {
			// top: 20px;
			transform: translateY(20px);
			visibility: visible;
			opacity: 1;
			transition: transform 0.3s, opacity 0.3s;
			transition-delay: 0.2s;
		}
	}
	.warehouse-dropmenu.hide-arrow:before {
		content: none;
	}
	.warehouse-dropmenu .status-result {
		width: 100%;
