input[type='range'] {
	width: 100%;
	height: 20px;
	overflow: hidden;
	cursor: pointer;
	outline: none;
	-webkit-appearance: none;
}

input[type='range']:focus {
	outline: none;
}

input[type='range'],
input[type='range']::-webkit-slider-runnable-track,
input[type='range']::-webkit-slider-thumb {
	-webkit-appearance: none;
	background: none;
}

input.min::-webkit-slider-thumb,
input.max::-webkit-slider-thumb {
	position: relative;
	margin-top: -1px;
	background: #787885;
	border: 1px solid white;
	height: 6px;
	width: 6px;
	cursor: pointer;
	-webkit-appearance: none;
	transition: 0.3s;
	border-radius: 10px;
	z-index: 2;
}

input.inputThumbColor::-webkit-slider-thumb {
	background: #aaaab3;
}

input[type='range']:nth-child(1)::-webkit-slider-thumb {
	z-index: 3;
}

input[type='range']::-webkit-slider-runnable-track {
	width: 100%;
	height: 4px;
	cursor: pointer;
	border-radius: 10px;
	/* box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d; */
	/* background: #3071a9; */
	background: linear-gradient(
		-90deg,
		#00cc00 18%,
		#e1d107 36%,
		#fd7777 55%,
		#b52318 80%,
		#860101 85%
	);
	/* border-radius: 1.3px; */
	/* border: 0.2px solid #010101; */
}

/* Proof of concept for Firefox */
@-moz-document url-prefix() {
	.min {
		position: absolute;
		top: 0 !important;
		overflow: visible !important;
		height: 0;
		z-index: 1;
	}

	.max {
		position: absolute;
		top: 0 !important;
		overflow: visible !important;
		height: 0;
		z-index: 2;
	}

	input.min::-moz-range-thumb,
	input.max::-moz-range-thumb {
		position: relative;
		margin-top: -1px;
		background: #787885;
		border: 1px solid white;
		height: 6px;
		width: 6px;
		cursor: pointer;
		-webkit-appearance: none;
		transition: 0.3s;
		border-radius: 10px;
		z-index: 2;
	}

	input.inputThumbColor::-moz-range-thumb {
		background: #aaaab3;
	}

	.min::-moz-range-thumb {
		transform: translateY(0px);
		z-index: 1;
	}

	.max::-moz-range-thumb {
		transform: translateY(0px);
		z-index: 2;
	}

	.min::-moz-range-track {
		background: linear-gradient(
			-90deg,
			#00cc00 18%,
			#e1d107 36%,
			#fd7777 55%,
			#b52318 80%,
			#860101 85%
		);
		width: 100%;
		height: 4px;
		cursor: pointer;
		border-radius: 10px;
		z-index: 1;
	}

	.max::-moz-range-track {
		background-color: transparent;
		width: 100%;
		height: 4px;
		cursor: pointer;
		border-radius: 10px;
		z-index: 2;
	}
}
