	width: 100%;
	z-index: 1;
	user-select: none;
	/* margin-top: -5px;
    margin-left: 20px; */
}
.rangeslider input {
	position: absolute;
}
.rangeslider .rangesInput {
	transform: rotate(90deg);
	position: absolute;
	// top: 32px;
	// height: 20px;
	// width: 80px;
	// right: -31px;
	// height: 20px;
	// width: 74px;
	// right: -29px;
  top: 36px;
    height: 20px;
    width: 82px;
    right: -33px;
}
.rangeslider .minBG {
	top: 8px;
	/* left: 0; */
	z-index: 1;
	/* content: ''; */
	left: 0;
	position: absolute;
	width: 0%;
	height: 4px;
	border-radius: 2px 0 0 2px;
	background-color: rgba(135, 132, 132, 0.7);
}
.rangeslider .maxBG {
	top: 8px;
	/* left: 0; */
	z-index: 1;
	/* content: ''; */
	position: absolute;
	border-radius: 0 2px 2px 0;
	width: 0%;
	height: 4px;
	right: 0;
	z-index: 1;
	background-color: rgba(135, 132, 132, 0.7);
}
.rangeslider .rangesBtnBlock {
	height: 45px;
	width: 100%;
	z-index: 5;
	position: absolute;
}
.rangeslider .inc {
	height: 18px;
	z-index: 5;
	top: 50px;
	width: 100%;
	display: flex;
	cursor: pointer;
	position: absolute;
}
.rangeslider .dec {
	height: 18px;
	top: 68px;
	z-index: 5;
	display: flex;
	width: 100%;
	cursor: pointer;
	position: absolute;
}
.rangeslider .rangesList {
	padding-left: 15px;
	padding-right: 10px;
	height: 18px;
	position: relative;
	cursor: pointer;
}
.rangesList.select-btn:before {
	content: '';
	width: 4px;
	height: 4px;
	top: 7px;
	left: 6px;
	border-radius: 100%;
	position: absolute;
	background-color: #787885;
}
.rangeslider .rangesList:hover {
	color: rgba(0, 0, 0, 0.5);
}
.rangeslider .dec:hover .inputDataMax,
.rangeslider .inc:hover .inputDataMin {
	color: rgba(0, 0, 0, 0.5);
}
.rangeslider .inputDataMin {
	padding: 0;
	padding-left: 15px;
	// transition: 0.2s;
	pointer-events: none;
	border: none;
	position: relative;
	outline: none;
	width:calc(100% - 40px);
	width: 7px;
	&::selection {
		background-color: #afafb3;
	}
}
.rangeslider .inputDataMax {
	padding: 0;
	padding-left: 15px;
	// transition: 0.2s;
	pointer-events: none;
	border: none;
	position: relative;
	outline: none;
	width:calc(100% - 40px);
	width: 7px;
	
	&::selection {
		background-color: #afafb3;
	}
}
.rangeslider .arrowsDec {
	position: absolute;
	transition: 0.2s;
	opacity: 0;
	left: 0;
	top: 3px;
}
.rangeslider .arrowsDec .arrowUp {
	position: absolute;
	background-image: url('../../img/arrow-down.svg');
	width: 6px;
	height: 3px;
	background-size: 100%;
	top: 5px;
	left: 5px;
	transition: 0.2s;
	opacity: 0.8;
	transform: rotate(180deg);
}
.rangeslider .arrowsDec .arrowDown {
	position: absolute;
	background-image: url('../../img/arrow-down.svg');
	width: 6px;
	height: 3px;
	background-size: 100%;
	transition: 0.2s;
	top: 7px;
	left: 5px;
	opacity: 0;
}
.rangeslider .arrowsInc {
	position: absolute;
	transition: 0.2s;
	opacity: 0;
	left: 0;
	top: 3px;
}
.rangeslider .arrowsInc .arrowUp {
	position: absolute;
	background-image: url('../../img/arrow-down.svg');
	width: 6px;
	height: 3px;
	background-size: 100%;
	top: 2px;
	left: 5px;
	transition: 0.2s;
	opacity: 0;
	transform: rotate(180deg);
}
.rangeslider .arrowsInc .arrowDown {
	position: absolute;
	background-image: url('../../img/arrow-down.svg');
	width: 6px;
	height: 3px;
	transition: 0.2s;
	background-size: 100%;
	top: 5px;
	left: 5px;
	opacity: 0.8;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
}

input[type='number'] {
	-moz-appearance: textfield;
}

